import {v4} from 'uuid';
import { SERVER_ADDRESS, put } from '../../remote';
export const DEFAULT_CONFIG_TYPE = 'DEFAULT_CONFIG_TYPE';

interface $state {
    addedActivities: {[id: string]: {
      inSchedule?: Boolean,
      ordinal: number,
    }},
    wontDoActivities: {[id: string]: Boolean},
    completedActivities: {[id: string]: Boolean},
    materials: {[id: string]: Boolean},
    materialTypes: {[id: string]: Boolean},
    dashboardTabs: {
      name?: string,
      ages: string[],
      subjects: string[],
      ordinal: number,
      touched: Boolean,
      uuid: string,
    }[]
}
const getDefaultState = ()=>{
  const rawAddedActivities = localStorage.getItem('addedActivities');
  const rawWontDoActivities = localStorage.getItem('wontDoActivities');
  const rawCompletedActivities = localStorage.getItem('completedActivities');
  const rawMaterials = localStorage.getItem('materials');
  const rawMaterialTypes = localStorage.getItem('materialTypes');
  const dashboardTabs = localStorage.getItem('dashboardTabs');

  const defaultState : $state = {
    addedActivities: rawAddedActivities ? JSON.parse(rawAddedActivities) : {},
    wontDoActivities: rawWontDoActivities ? JSON.parse(rawWontDoActivities) : {},
    completedActivities: rawCompletedActivities ? JSON.parse(rawCompletedActivities) : {},
    materials: rawMaterials ? JSON.parse(rawMaterials) : {},
    materialTypes: rawMaterialTypes ? JSON.parse(rawMaterialTypes) : {},
    dashboardTabs: dashboardTabs ? JSON.parse(dashboardTabs) : [
      {
        ages: [],
        subjects: [],
        ordinal: 0,
        touched: false,
        uuid: v4()
      }
    ]
  };
  return defaultState;
}

export const reducer = (state: $state, action: any)=>{
  if(state === undefined){
    state = getDefaultState()
  }
  if(action.type === DEFAULT_CONFIG_TYPE){
    if(action.payload.flush){
      return action.payload.data
    }
    const nextState = Object.keys(action.payload).reduce((acc: any, curr: string)=>{
      acc[curr] = action.payload[curr];
      localStorage.setItem(curr, JSON.stringify(action.payload[curr]))
      return acc;
    }, {...state});
    put(SERVER_ADDRESS+'/users/config', {
      body: action.payload
    });
    return nextState;
  }
  return state;
}

export const createConfigAction = (payload: any)=>{
  return {
    type: DEFAULT_CONFIG_TYPE,
    payload,
  }
}

interface $globalState {
  config: $state
}

export const configSelector = (state: $globalState)=>state.config
export const dashboardTabsSelector = (state: $globalState)=>state.config.dashboardTabs;
export const materialsSelector = (state: $globalState)=>state.config.materials;
export const materialTypesSelector = (state: $globalState)=>state.config.materialTypes;
export const addedActivitiesSelector = (state: $globalState)=>state.config.addedActivities;
export const wontDoActivitiesSelector = (state: $globalState)=>state.config.wontDoActivities;
export const completedActivitiesSelector = (state: $globalState)=>state.config.completedActivities;
export const addedActivitySelector = (id: string)=>(state: $globalState)=>state.config.addedActivities[id];
export const wontDoActivitySelector = (id: string)=>(state: $globalState)=>state.config.wontDoActivities[id];
export const completedActivitySelector = (id: string)=>(state: $globalState)=>state.config.completedActivities[id];
