import { createStore, applyMiddleware, compose } from 'redux'
import thunk from 'redux-thunk';
import { reducer as config } from './config';
import { reducer as user } from './user';
import { reducer as global } from './global';
import { reducer as ui } from './ui';

const composeEnhancers =
  typeof window === 'object' &&
  (window as any).__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ ?   
    (window as any).__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({
      // Specify extensionâ€™s options like name, actionsBlacklist, actionsCreators, serialize...
    }) : compose;

const enhancer = composeEnhancers(
  applyMiddleware(thunk),
);

const reducerDic : any = {
  config,
  user,
  global,
  ui
};

const reducer = (state: any = {}, action: any)=>{
  return Object.keys(reducerDic).reduce((acc, key)=>{
    acc[key] = reducerDic[key](state[key], action);
    return acc;
  }, state)
}

export const storeGenerator = ()=>createStore((state, action)=>{
  if(action.type === 'RESET'){
    localStorage.clear();
    return reducer(undefined, action);
  }
  return reducer(state, action);
}, enhancer);

export const store = storeGenerator()

export const dispatch = store.dispatch