export const DEFAULT_GLOBAL_TYPE = 'DEFAULT_GLOBAL_TYPE';

const defaultState = {
  snackbar: {
    message: '',
    id: ''
  },
};

export const reducer = (state=defaultState, action: any)=>{
    if(action.type === DEFAULT_GLOBAL_TYPE){
      return Object.keys(action.payload).reduce((acc: any, key)=>{
        acc[key]=action.payload[key];
        return acc
      }, {...state})
    }
    return state;
}

export const createGlobalAction = (payload: any)=>{
  return {
    type: DEFAULT_GLOBAL_TYPE,
    payload,
  }
}



export const globalSelector = (state: any)=>state.global