import jwt from 'jsonwebtoken';
import { getToken, setToken } from '../../auth';

export const DEFAULT_USER_TYPE = 'DEFAULT_USER_TYPE';

const getDefaultState = ()=>{
  const token = getToken();
  return token ? jwt.decode(token) : null
}

export const reducer = (state: any, action: any)=>{
  if(state === undefined){
    state = getDefaultState();
  }
  if(action.type === DEFAULT_USER_TYPE){
    setToken(action.payload);
    if(!action.payload) return action.payload;
    return jwt.decode(action.payload)
  } 
  return state;
}

export const createUserAction = (payload: any)=>{
  return {
    type: DEFAULT_USER_TYPE,
    payload,
  }
}



export const userSelector = (state: any)=>state.user