export const DEFAULT_UI_TYPE = 'DEFAULT_UI_TYPE';

const getDefaultState = ()=>{
  const ui = localStorage.getItem('ui');

  const defaultObj = {
    dashboard: {
      tab: 0,
      isEditing: true
    },
    addedActivities: {
      tab: 0,
    },
    credits: {
      count: 0,
      hasNew: false
    }
  };
  return ui ? {
    ...defaultObj,
    ...JSON.parse(ui)
  } : defaultObj;
}




export const reducer = (state: any, action: any)=>{
  if(state === undefined){
    state = getDefaultState();
  }
  if(action.type === DEFAULT_UI_TYPE){
    const nextState = Object.keys(action.payload).reduce((acc: any, key)=>{
      acc[key]=action.payload[key];
      return acc
    }, {...state})
    localStorage.setItem('ui', JSON.stringify(nextState))
    return nextState
  }
  return state;
}

export const createUIAction = (payload: any)=>{
  return {
    type: DEFAULT_UI_TYPE,
    payload,
  }
}

export const uiSelector = (state: any)=>state.ui
export const uiCreditsSelector = (state: any)=>state.ui.credits