import 'fontsource-roboto';

import React from 'react';
// import { BrowserRouter } from 'react-router-dom';
import { HashRouter } from 'react-router-dom';
import { SWRConfig } from 'swr';
import { Provider } from 'react-redux';

import { store } from './utils/redux';
import { get } from './utils/remote';
import RootRoutes from './features/components/routes/Root';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';

import DateFnsUtils from '@date-io/date-fns';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import { blue } from '@material-ui/core/colors';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: blue[700],
    },
  },
});
function App() {
  return <Wrap store={store}><RootRoutes /></Wrap>;
}

export default App;

const Router = HashRouter;

export function Wrap({children, store}: { children: JSX.Element, store: any}){
  return (<SWRConfig
    value={{
      // refreshInterval: 3000,
      fetcher: (arg1, ...args)=>{
        return get(arg1, ...args)
      },
    }}
    >
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <ThemeProvider theme={theme}>
        <Provider store={store}>
          <div>
            <Router>
              {
                children
              }
            </Router>
          </div>
        </Provider>
      </ThemeProvider>
    </MuiPickersUtilsProvider>
  </SWRConfig>);
}